.difficulty-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: rgb(227, 253, 253);
}

.difficulty-header {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 25px 0px;
  background-color: rgb(160, 210, 235);
  box-shadow: 0 0 10px 2.5px rgba(0, 0, 0, 0.25);
}

.difficulty-header .difficulty-title-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.difficulty-header .difficulty-title-container .difficulty-title {
  font-weight: bold;
  font-size: 3rem;
}

.difficulty-container {
  flex: 5;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.difficulty-container .door {
  text-decoration: none;
  background-color: rgb(160, 210, 235);
  color: rgb(247, 251, 252);
  font-size: 4rem;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500px;
  border-radius: 8px;
  margin-left: 25px;
  cursor: pointer;
  box-shadow: 0 0 5px 2.5px rgba(0, 0, 0, 0.25);
}

.difficulty-container .door:active {
  box-shadow: none;
}

.difficulty-container .door-4 {
  margin-right: 25px;
}

.difficulty-container .door:hover {
  background-color: rgba(185, 215, 234, 0.685);
}
